import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Territory(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/territory.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[-8.01, -0.61, 5.46]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere151.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere151_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere151_2.geometry}
          material={materials.outline}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere151_3.geometry}
          material={materials["glass.005"]}
        />
      </group>
    </group>
  );
}
export default Territory;
useGLTF.preload("/glossarySelected/territory.glb");
